import React from "react";
import { Element } from "react-scroll";
//import "../.././ima/web.webp"
import Image from "../../ima/web.webp"
import  LinearProgress  from "@mui/material/LinearProgress";
import "./Skillcontainer.css"
const Skillcontainer=()=>{
    return(
        <div  className="yuhan">
     <Element className="skillcontainer" id="skills"> </Element>
       <div className="skillcontainer__image">
        <img src={Image} alt=""></img>
        </div> 
        <div className="skillcontainer__text">
            <h2>SKILLSET</h2>
            <div className="skillcontainer__skillset">
                <h5>React</h5>
                <div className="skillcontainer__slider1">
                <LinearProgress variant="determinate" value={70}></LinearProgress>
                </div>
                 </div>
                 <div className="skillcontainer__skillset">
                <h5>NodeJs</h5>
                <div className="skillcontainer__slider2">
                <LinearProgress variant="determinate" value={75}></LinearProgress>
                </div>

            </div>
            <div className="skillcontainer__skillset">
                <h5>Redux</h5>
                <div className="skillcontainer__slider3">
                <LinearProgress variant="determinate" value={20}></LinearProgress>
                </div>

            </div>
            <div className="skillcontainer__skillset">
                <h5>JavaScript</h5>
                <div className="skillcontainer__slider4">
                <LinearProgress variant="determinate" value={90}></LinearProgress>
                </div>

            </div>
            <div className="skillcontainer__skillset">
                <h5>CSS</h5>
                <div className="skillcontainer__slider5">
                <LinearProgress variant="determinate" value={90}></LinearProgress>
                </div>

            </div>
            <div className="skillcontainer__skillset">
                <h5>HTML</h5>
                <div className="skillcontainer__slider6">
                <LinearProgress variant="determinate" value={90}></LinearProgress>
                </div>

            </div>
        </div>
       
        </div>
    )
}
export default Skillcontainer;